@charset 'utf-8';

// import
@import '_reset.scss';

// layout
#container {
  width: 100vw;
  height: auto;
  min-height: 100vh;
  margin: 0 auto;
  position: relative;
}

.inner {
  width: 960px;
  height: auto;
  margin: 0 auto;
}

// font-size
h1 {
  font-size: 1.4rem;
}

h2 {
  font-size: 2.0rem;
}

p {
  font-size: 1.4rem;
}

a {
  font-size: 1.4rem;
}

// header
.header {
  width: 100%;
  height: auto;
  background: #333;
  position: fixed;
  a {
    color: #fff;
    padding: 1.5rem;
    &:hover {
      text-decoration: underline;
    }
  }
  .inner {
    display: flex;
    justify-content: space-between;
    .info {
      display: flex;
      align-items: center;
      h1 {
        margin-right: 2rem;
      }
    }
    .nav {
      ul {
        display: flex;
        align-items: center;
        li {
          display: flex;
          align-items: center;
          &:first-of-type {
            margin-right: 2rem;
          }
        }
      }
    }
  }
}

// footer
footer {
  width: 100%;
  height: auto;
  background: #2c3d4f;
  position: absolute;
  bottom: 0;
  .inner {
    padding: 3rem;
    p {
      text-align: center;
      font-size: 1.4rem;
      color: #fff;
      &:first-child {
        margin-bottom: 1.5rem;
      }
    }
  }
}

// main
.main {
  padding-bottom: 107px;
}

.main-header {
  width: 100%;
  height: auto;
  padding: 20rem;
  background-image: url("../images/header.jpg");
  background-size: cover;
  background-attachment: fixed;
  p {
    text-align: center;
    img {
      width: auto;
      height: 8rem;
    }
  }
}

.sec {
  .inner {
    padding: 3rem;
    text-align: center;
    h2 {
      margin-top: 1.5rem;
      margin-bottom: 3rem;
    }
    > p {
      margin-bottom: 1.5rem;
      line-height: 2.0;
    }
  }
}

.function {
  background: #d3d3d3;
  .inner {
    .function-images {
      display: flex;
      justify-content: space-between;
      padding: 3rem 12rem;
      margin-bottom: 3rem;
      .function-image {
        img {
          display: inline-flex;
          width: auto;
          height: 10rem;
        }
        p {
          margin-top: 1.5rem;
        }
      }
    }
  }
}